import { MatPaginatorIntl } from '@angular/material/paginator';

const rangeLabel = (page: number, pageSize: number, length: number) => {
  if (length === 0 || pageSize === 0) {
    return `0 de ${length}`;
  }

  length = Math.max(length, 0);
  const startIndex = page * pageSize;
  const endIndex = startIndex < length ? Math.min(startIndex + pageSize, length) : startIndex + pageSize;

  return `${startIndex + 1} - ${endIndex} de ${length}`;
};

export function getPtBrPaginatorIntl() {
  const paginatorIntl = new MatPaginatorIntl();

  paginatorIntl.itemsPerPageLabel = 'Registros por pgina';
  paginatorIntl.firstPageLabel = 'Primeira pgina';
  paginatorIntl.nextPageLabel = 'Prxima pgina';
  paginatorIntl.previousPageLabel = 'Voltar pgina';
  paginatorIntl.lastPageLabel = 'ltima pgina';

  paginatorIntl.getRangeLabel = rangeLabel;

  return paginatorIntl;
}
